import React from "react";
import "./Link.css";

const linkList = [
    {key: 1 , href: "#AboutMe", text : ".aboutMe"},
    {key: 2 , href: "#SkillList", text : ".getSkill"},
    {key: 3 , href: "#ProjectList", text : ".getProject"},
    {key: 4 , href: "#ContactMe", text : ".contactMe"},
];

function Link() {

    const linkClickHandler = (e) =>{
        let list = Array.from(e.target.parentElement.children);
        let bar = document.getElementById("headerBar");
        let left = 0;
        for(let i=0; i<e.target.id-1; i++){
            left = left + list[i].offsetWidth;
        }
        list.forEach((item) => {
            item.classList.remove("active");
        })

        bar.style.width = `${e.target.offsetWidth}px`;
        bar.style.height = `${e.target.offsetHeight}px`;
        bar.style.left = `${left}px`;
        e.target.classList.add("active")

    }

	return (
		<div className="link-container">
            {
            linkList.map((link) => {
                return (
                    <a id={link.key} key={link.key} href={link.href} onClick={linkClickHandler} >{link.text}</a>
                );
            })
            }
            <div className="bar" id="headerBar"></div>
		</div>
	);
}

export default Link;
