import React from 'react';
import '../Screen.css';
import './Content_1.css'

import scrollReveal from "scrollreveal";

export default function Content1(){

    const sectionRef = React.useRef(null);
    React.useEffect(() => {
        if (sectionRef.current)
        scrollReveal().reveal(sectionRef.current, {
            reset: true,
            delay: 500
        });
    }, []);

    let s1 = "<Showcase about Work + Life/>";
    let s2 = "Art is not what you see, but what you make others see.";
    let s3 = " - Edgar Degas."

    return(
        <React.Fragment>
            <div className="section-left">

            </div>

            <div ref={sectionRef} className="section-right">
                <h1 className="title">WilliamKMP<span>.is()</span> </h1>
                <h3>{s1}</h3>
                <h3>{s2}</h3>
                <h3>{s3}</h3>
            </div>
        </React.Fragment>
    );
}


