import React from 'react';
import '../../Screen.css';
import './Project.css';

export default function Project({imgSrc, title, text, desc}){
    return(
        <div  className = "work-item-container">
            <div className="image-container">
                <img src={imgSrc} alt=" " />
            </div>
            <article>
                <h1 className="title"><span>{title}</span></h1>
                <h2>{text}</h2>
                {
                    desc.map((line) =>{
                        return(<h2 className="t-white">{line}</h2>);
                    })
                }
            </article>
        </div>
    );
}