import React from "react";
import "../Screen.css";
import "./Content_2.css";
import portrait from "./portrait.jpeg";

import scrollReveal from "scrollreveal";

export default function Content2() {

	const sectionRef = React.useRef(null);
    React.useEffect(() => {
        if (sectionRef.current)
        scrollReveal().reveal(sectionRef.current, {
            reset: false,
            delay: 500
        });
    }, []);

	

	let s1 = "<Hello!, I'm a UI/UX Designer and a Frontend Developer/>";
	let s2 = "//I'm an undergarduate at Bina Nusantara University studying computer science.";
	return (
		<React.Fragment>
			<div className="section-left">
				<div  className="portrait-container">
					<img  className="portrait" src={portrait} alt=" " />
				</div>
			</div>

			<div ref={sectionRef} className="section-right">
				<h1 className="title">
					William<span>.getInfo()</span>
				</h1>
				<h3 className = "t-white">{s1}</h3>
				<h2>{s2}</h2>
                <h2>//I have been studying and honing my skills in <span>Frontend Development</span> and <span>UI/UX Design</span></h2>
                <h2>//for 3+ years. I'm <span>flexible</span> and work great in <span>teams</span>.</h2>
			</div>
		</React.Fragment>
	);
}
