import React from 'react';
import '../Screen.css';
import './Content_4.css';
import scrollReveal from "scrollreveal";
import Tag from './ProjectComp/Project';
import WebShooterImgPath from './images/WebShooterMockup.png'
import ASLTranslatorImgPath from './images/asl.png';
import FoodU from './images/FudU.png';


const data = [
    {
        src: FoodU,
        title: "FoodU",
        text: "//currently being developed (2019, now)",
        desc: ["A mobile progressive web app build by me and my group Muhamad Faqih, Andre Wijaya and Aisyah Ramadhania. It can help users to search, view and share cooking recipe and check the food nutritional value. Foodu is currently still under developement"]
    },
    {
        src: ASLTranslatorImgPath,
        title: "ASL to Text",
        text: "//developed (2019)",
        desc: ["A dynamic responsive web app where user can translate text from text to ASL language. This web app implements responsive design and JavaScript naimation and DOM manipulation"]
    },
    {
        src: WebShooterImgPath,
        title: "MetaBalls",
        text: "//developed (2021)",
        desc: ["A web app game with infinite playtime similar to Asteroid, where player shoot and evade the enemies to survive and gain score. Thiw web app uses HTML and JavaScript Canvas, OOP concept and design pattern"]
    }
];

export default function Content1(){
    const sectionRef = React.useRef(null);
    React.useEffect(() => {
        if (sectionRef.current)
        scrollReveal().reveal(sectionRef.current, {
            reset: false,
            delay: 500
        });
    }, []);

    return(
        <React.Fragment>
            <div className="section-left">

            </div>

            <div ref={sectionRef} className="section-right">
                <h1 className="title">William<span>{`.getWorks()`}</span></h1>
                <h3 className="title">//List of project and works that I have done.</h3>
                <div className="project-container">
                    {
                        data.map((work) => {
                            return (<Tag 
                                title = {work.title}
                                imgSrc = {work.src}
                                text = {work.text}
                                desc = {work.desc}
                            ></Tag>);
                        })
                    }
                </div>
            </div>
        </React.Fragment>
    );
}